<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入名称、关键词、描述"
                      @keyup.native.enter="initData"
                      v-model="categoryQuery.queryStr">
            </el-input>
            <el-button class="filter-item" @click="initData" size="small">查询</el-button>
            <el-button type="primary" @click="showAddDialog" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="80"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="120">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getCategory(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id)" v-if="permission.delete">删除
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="categoryQuery.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="categoryQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="修改文章类型信息" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="categoryData" :rules="rules" ref="categoryData">
                <el-form-item :label-width="itemWidth" label="*类型名称" prop="name">
                    <el-input class="input-short" v-model="categoryData.name"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="关键词" prop="keyWord">
                    <el-input class="input-short" v-model="categoryData.keyWord" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="描述" prop="description">
                    <el-input class="input-short" v-model="categoryData.description" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*类型名称-英文" prop="nameEn">
                    <el-input class="input-short" v-model="categoryData.nameEn"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="关键词-英文" prop="keyWordEn">
                    <el-input class="input-short" v-model="categoryData.keyWordEn" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="描述-英文" prop="descriptionEn">
                    <el-input class="input-short" v-model="categoryData.descriptionEn" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';


    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                categoryData: {
                    id: '',
                    name: '',
                    keyWord: '',
                    description: '',
                    nameEn: '',
                    keyWordEn: '',
                    descriptionEn: '',
                    type: '3'
                },
                action: '',
                rules: {
                    name: [{validator: validate, trigger: 'blur'}],
                    nameEn: [{validator: validate, trigger: 'blur'}]
                },
                categoryQuery: {
                    queryStr: '',
                    pageNum: 1,
                    pageSize: 10,
                    type: '3'
                },
                total: 0,
                tableData: [],
                tableHeader: [{
                    label: "类型名称",
                    prop: "name",
                    width: 200
                }, {
                    label: "关键词",
                    prop: "keyWord",
                    width: 300
                }, {
                    label: "描述",
                    prop: "description",
                    width: 300
                }, {
                    label: "类型名称-英文",
                    prop: "nameEn",
                    width: 200
                }, {
                    label: "关键词-英文",
                    prop: "keyWordEn",
                    width: 200
                }, {
                    label: "描述-英文",
                    prop: "descriptionEn",
                    width: 200
                }]
            }
        },
        created() {
            this.initData();
        },
        methods: {
            addCategory() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory',
                        method: 'post',
                        data: _this.categoryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddDialog() {
                this.dialogVisible = true;
                this.action = 'add';
            },
            validate() {
                this.$refs['categoryData'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addCategory();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.categoryData) {
                    this.categoryData[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                this.$refs['categoryData'].resetFields();
                for (key in this.categoryData) {
                    switch (key) {
                        case 'type':
                            this.categoryData[key] = '3';
                            break;
                        default:
                            this.categoryData[key] = '';
                    }
                }
                this.action = '';
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/systemcategory',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            deleteCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showDeleteDialog(id) {
                this.$confirm('确认删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteCategory(id);
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory',
                        method: 'put',
                        data: _this.categoryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/systemcategory/query',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            initData() {
                if (this.categoryQuery.queryStr === '') {
                    this.getList();
                } else {
                    this.query();
                }
            },
            //翻页的按钮
            userPage(val) {
                this.categoryQuery.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.categoryQuery.pageSize = val;
                this.initData();
            },
        }
    }
</script>



